.related-objects-list {
    margin-bottom: 16px;

    .column:empty { padding: 0; }
    &.isLoading {
        opacity: .2;
        pointer-events: none;
    }

    & ~ .related-objects-list {
        padding: $gutter * .5;
        background-color: $gray-800;
        margin: 0 0 $gutter * 2;
        border-radius: $gutter * .5;
        border: 1px solid $gray-700;
        .column:not(:last-child) {
            flex-shrink: 0;
            flex-basis: 12 * $gutter;
            article { position: relative; padding-bottom: $gutter * 1.5; }
            footer {
                @extend .full-size-absolute;
                a {
                    @extend .full-size-absolute;
                    display: flex;
                    justify-content: center;
                    align-items: flex-end;
                    padding-bottom: $gutter * .25;
                }
            }
        }
    }

    main.panel-is-open .module-form & {
        cursor: auto;
        pointer-events: none;
    }
}



// single related object
.related-object {
    position: relative;

    article {
        display: flex;
        flex-direction: column;
        box-shadow: none;

        &.unselectable {
            pointer-events: none;
            opacity: .4;
        }


        header {
            display: flex;
            justify-content: space-between;
            margin-bottom: $gutter;
            h1 {
                margin: 0;
                padding-right: $gutter;
                word-break: break-word;
                font-size: $font-size-sm;
                font-weight: $font-weight-light;
                line-height: $font-size-lg;
                min-height: $font-size-lg * 2;
                &:empty {
                    content: 'untitled';
                    opacity: .5;
                }
            }
            .prop-id {
                opacity: .5;
                font-size: $font-size-sssm;
                line-height: $gutter;
                white-space: nowrap;
            }
        }

        >div {
            &:not(:last-of-type) {
                margin-bottom: $gutter;
            }
            &.thumbnail {
                display: none;
                figure {
                    // placeholder in attesa di mettere il thumb
                    background-color: $gray-900;
                    position: relative;
                    width: 100%;
                    height: 0;
                    padding-bottom: 56%; // 16 / 9
                    overflow: hidden;
                    img {
                        position: absolute;
                        top: 0; left: 50%;
                        display: block;
                        height: 100%;
                        transform: translateX(-50%);
                    }
                }
            }
        }

        &.images {
            div.thumbnail {
                display: block;
            }
        }

        footer {
            position: relative;
            display: flex;
            justify-content: flex-end;
            margin-top: $gutter * 1.5;
            a { opacity: .5; line-height: 1rem; cursor: pointer; }
            a:hover { opacity: 1; }
        }
    }

    > footer{
        position: absolute;
        transform: translateY(-100%);
        padding-bottom: $gutter * .5;
        padding-left: $gutter * .5;
    }

    &.removed {
        article {
            opacity: .2;
        }
    }
}



// in panel
.relations-add {
    display: flex;
    flex-direction: column;
    height: 100%;

    // layout
    > nav, >header.tab, >footer {
        flex-shrink: 0;
    }

    > nav {
        display: none; // for filters whenever...
    }

    > header.tab {
        margin-right: $gutter;
        margin-left: $gutter;
        margin-top: $gutter;
        margin-bottom: 0;
        cursor: auto;
    }
    .related-objects-list {
        flex-grow: 1;
        overflow: auto;
        margin-right: 0;
        margin-left: $gutter * .5;
        margin-top: $gutter;
        padding-right: $gutter * .5;
    }

    > footer {
        padding: $gutter;
    }

    // ui specs
    .related-object article {
        position: relative;
        background-color: $gray-700;
        cursor: pointer;

        &.selected { background-color: $info; }

        footer {
            justify-content: center;
            width: calc(100% + 16px);
            margin-left: -8px;
            margin-bottom: -8px;
            height: 24px;
            a {
                justify-content: center;
                display: inline-flex;
                align-items: center;
                width: 100%;
                height: 100%;
                background-color: $gray-800;
            }
            &:hover {
                a {
                    background-color: $gray-600;
                }
            }
        }
    }
}

