.element-menu {
    > * {
        margin: 0 0 $gutter 0;
    }
    > *:empty {
        display: none;
    }

    > .project {
        display: block;
        width: 282px;
        height: 282px;
        overflow: hidden;
        padding: 140px 1rem 1rem;
        margin-bottom: $gutter * 1.5;
        border-bottom: none;
        color: #bd8411;

        &.dark {
            background-image: url("data:image/svg+xml;base64,");
            background-color: $gray-900;
        }

        background-image: url("data:image/svg+xml;base64,");
        background-color: $white;
        background-size: 50%;
        background-repeat: no-repeat;
        background-position: 1rem 1rem;
        font: normal 1.5em $font-family-serif;
        word-break: break-word;

        // andranno delle variabili per i breakpoint delle mediaquery
        @media screen and (max-width: 960px) {
            width: 140px;
            height: 140px;
            padding: 75px .5rem .5rem;
            background-position: .5rem .55rem;
            margin-right: $gutter;
        }

        body.view-module & {
            width: 158px;
            height: 148px;
            padding: 88px 1rem 1rem;
            background-position: 1rem 1rem;
            border-right: 1rem solid $gray-600;
        }
    }

    > .menu-links {
        list-style: none;
        padding: 0;
        li {
            margin: $gutter 0;
        }
    }

    > .menu-actions {
        list-style: none;
        padding: 0;

        a {
            display: block;

            &:hover {
                color: white;
            }
        }

        li {
            line-height: 1.5em;
            padding: .5em 0;
            border-bottom: 1px solid $gray-700;

            &:first-child {
                padding-top: 0;
            }

            &:last-child {
                border-bottom: none;
            }
        }
    }
}


